import React, { Component } from 'react';
import { Form, Modal, Upload, Button, Icon, message, Table } from 'antd';
import { isEmpty } from 'lodash';
import { uploadFile, saveMassStaffs } from 'services/teacher';

@Form.create({ name: 'SearchBar' })
class PopupImportFile extends Component {
  state = {
    data: [],
    loading: false,
    saving: false,
  };

  beforeUpload = async file => {
    console.log(file)
    this.setState({ loading: true });
    const formData = new FormData();
    formData.append('file', file);
    formData.append('name', file.name);
    formData.append('fileName', file.name);
    const result = await uploadFile(formData);
    console.log(result)
    if(result.data && result.data.savedStaffs) {
      this.setState({ data: result.data.savedStaffs });
      message.success('Tải lên thành công');
    }
    this.setState({ loading: false });
  }

  handleCancel = () => {
    console.log("handle cancel")
    this.props.closePopup();
  }

  handleSave = async () => {
    this.setState({ saving: true });
    try {
      if(!isEmpty(this.state.data)) {
        await saveMassStaffs(this.state.data)
      }
    } catch(err) {
      message.error("Có lỗi xãy ra");
    }
    this.setState({ saving: false });
  }

  render() {
    const { data, loading, saving } = this.state;
    const { visible } = this.props;
    const columns = [
      {
        title: 'Khoa',
        dataIndex: 'faculty',
        key: 'faculty',
        width: '200px',
      },
      {
        title: 'Bộ môn',
        dataIndex: 'subjectGroup',
        key: 'subjectGroup',
        width: '200px',
      },
      {
        title: 'Mã cán bộ',
        dataIndex: 'staffId'
      },
      {
        title: 'Họ và tên',
        dataIndex: 'fullname'
      },
      {
        title: 'Giới tính',
        dataIndex: 'genderText'
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'birthdate'
      }
    ];
    console.log(data)
    return (
      <Modal
        title="Nhập dữ liệu từ file Excel"
        visible={visible}
        width={900}
        footer={[
          <Button key="back" onClick={this.handleCancel}>
            Hủy
          </Button>,
          <Button key="submit" type="primary" loading={saving} onClick={this.handleSave}>
            Lưu dữ liệu
          </Button>,
        ]}
      >
        <Upload
          fileList={[]}
          beforeUpload={this.beforeUpload}
        >
          <Button loading={loading} >
            <Icon type="upload" /> Chọn file
          </Button>
        </Upload>
        <Table columns={columns} dataSource={data} loading={loading} />
      </Modal>
    );
  }
}

export default PopupImportFile;



